.container {
    display: flex;
    flex-direction: column;
    margin: var(--size-epic) 0 0 var(--size-epic);
    width: 100%;
}

@media (max-width: 720px) {
    .container {
        margin: var(--size-small) 0 var(--size-small) 0;
    }
}

.content-wrapper {
    display: flex;
    text-align: left;
    flex-flow: column;
    justify-content: space-between;
    z-index: 1;
    margin-left: var(--size-medium);
    max-width: 700px;
}

.header {
    font-size: var(--size-bigger);
    margin-bottom: var(--size-medium);
    line-height: 1.2;
}

@media (max-width: 400px) {
    .header {
        font-size: var(--size-larger);
    }
}

.sub-content {
    font-size: var(--size-large);
    font-weight: normal;
    text-align: left;
}

.logo {
    height: 40vmin;
    pointer-events: none;
    filter: blur(5px);
}

@media (max-width: 720px) {
    .home-title {
        margin-top: var(--size-huge);
        margin-right: var(--size-large);
    }
}

.home-title span {
    position: relative;
    display: block;
    border-radius: var(--size-small);
}