:root {
  --bg-color: white;

  --size-micro: 0.125rem;
  --size-tiny: 0.25rem;
  --size-small: 0.5rem;
  --size-medium: 0.75rem;
  --size-medium-plus: 0.85rem;
  --size-large: 1rem;
  --size-largeish: 1.25rem;
  --size-larger: 1.75rem;
  --size-big: 2rem;
  --size-bigger: 2.5rem;
  --size-huge: 4rem;
  --size-epic: 8rem;

  --small-bp: 720px;
  --medium-bp: 1080px;
}

.App {
  text-align: center;
  position: absolute;
  overflow: hidden;
  width: 100%;
}

.App-header {
  background: transparent;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.doggy {
  position: absolute;
  width: 300px;
  bottom: 50px;
  right: 200px;
  opacity: 0.3;
}


@media (max-width: 720px) {
  .doggy {
    width: 200px;
    right: 100px;
  }
}